import React from "react";

import styled from "@emotion/styled";

import Image from "@components/Image";

import mediaqueries from "@styles/media";

import SocialLinks from "@components/SocialLinks";

export default function AuthorHero({ author }){
    return (
        <Hero>
          <HeroImage>
            <RoundedImage src={author.avatar.large} />
          </HeroImage>
          <Heading>{author.name}</Heading>
          <Subheading dangerouslySetInnerHTML={{__html: author.bio}}></Subheading>
          <Social>
            <SocialLinks links={author.social} />
          </Social>
          <hr style={{marginTop: 50, width: 100}}></hr>
          <Heading2>Education</Heading2>

          <Heading3>Institute of Technology, Nirma University</Heading3>
          <Subheading dangerouslySetInnerHTML={{__html: 'Bachelors of Technology in Computer Science and Engineering'}}></Subheading>
          <Year>2018-2022</Year>

          <hr style={{marginTop: 50, width: 100}}></hr>
          <Heading2>Experience</Heading2>

          <Heading3>Technical Head</Heading3>
          <Subheading dangerouslySetInnerHTML={{__html: 'CSI Students Chapter, Nirma University'}}></Subheading>
          <Year>2020-2021</Year>

          <Heading3>WebMaster</Heading3>
          <Subheading dangerouslySetInnerHTML={{__html: 'ACM Students Chapter Nirma University'}}></Subheading>
          <Year>2019-2020</Year>
        </Hero>
      );
};

const Hero = styled.div`
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 35px auto 110px;
`;

const HeroImage = styled.div`
  position: relative;
  z-index: 1;
  height: 164px;
  width: 164px;
  margin-bottom: 35px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid ${p => p.theme.colors.background};
  box-shadow: 0px 15.619px 31.2381px rgba(0, 0, 0, 0.15);

  ${mediaqueries.tablet`
    width: 146px;
    height: 146px;
  `}

  ${mediaqueries.phablet`
    width: 136px;
    height: 136px;
    margin-bottom: 25px;
  `}
`;

const RoundedImage = styled(Image)`
  border-radius: 50%;
`;

const Heading = styled.h1`
  font-size: 38px;
  font-family: ${p => p.theme.fonts.sansSerif};
  color: ${p => p.theme.colors.primary};
  margin-bottom: 15px;
  font-weight: 600;

  ${mediaqueries.tablet`
  `}

  ${mediaqueries.phablet`
  `}
`;

const Heading2 = styled.h2`
  font-size: 30px;
  font-family: ${p => p.theme.fonts.sansSerif};
  color: ${p => p.theme.colors.primary};
  margin-top: 50px;
  margin-bottom: 15px;
  font-weight: 600;

  ${mediaqueries.tablet`
  `}

  ${mediaqueries.phablet`
  `}
`;

const Heading3 = styled.h3`
  font-size: 25px;
  font-family: ${p => p.theme.fonts.sansSerif};
  color: ${p => p.theme.colors.primary};
  margin-top: 25px;
  font-weight: 600;
  text-align: center;

  ${mediaqueries.tablet`
  `}

  ${mediaqueries.phablet`
  `}
`;

const Year = styled.h5`
  font-size: 15px;
  margin-top: 5px;
  font-family: ${p => p.theme.fonts.sansSerif};
  color: ${p => p.theme.colors.primary};
  font-weight: 100;

  ${mediaqueries.tablet`
  `}

  ${mediaqueries.phablet`
  `}
`;

const Subheading = styled.p`
  margin: 0 auto;
  max-width: 450px;
  color: ${p => p.theme.colors.grey};
  font-size: 18px;
  font-family: ${p => p.theme.fonts.sansSerif};
  line-height: 1.4;
  text-align: center;

  ${mediaqueries.phablet`
    font-size: 14px;
  `}
`;

const Social = styled.div`
  display: flex;
  align-items: center;
  margin-top: 35px;

  ${mediaqueries.phablet`
    font-size: 14px;
  `}
`;